#page-notice {
    .notice-cont {
        position: relative;
        width: 70%;
        margin: auto;
        @media (max-width: 1025px) {
            width: 90%;
        }
        @media (max-width: 800px) {
            width: 100%;
        }

        .group_ {
            justify-content: flex-start;
            min-height: 400px;
            @media (max-width: 1151px) and (min-width: 768px) {
                justify-content: space-between;
            }
            @media (max-width: 768px) {
                justify-content: center;
            }

            .__item {
                width: 245px;
                height: max-content;
                margin: 0.5rem;
                margin-bottom: 1rem;

                @media (max-width: 1151px) and (min-width: 1107px) {
                    width: 235px;
                }

                @media (max-width: 1107px) and (min-width: 1025px) {
                    width: 216px;
                }

                @media (max-width: 900px) and (min-width: 800px) {
                    width: 216px;
                }

                @media (max-width: 555px) {
                    width: 320px;
                }
            }

            .no-data {
                width: 100%;
                text-align: center;
                color: #ccc;
            }
        }
    }
}
